.mortgage-container { 
  text-align: center; 
  max-width: 700px; 
  margin: 0 auto; 
  padding: 2rem 1rem;
  background: var(--color-dark-bgCard);
  color: var(--color-dark-text);
  border-radius: var(--radius-lg);
}

.quiz-title { 
  font-size: 1.7rem; 
  margin-bottom: 1rem;
  color: var(--color-dark-text);
}

.option-grid { 
  display: flex; 
  flex-direction: column; 
  gap: .75rem; 
  margin: 1.5rem 0; 
}

.reveal-text { 
  margin: 1.25rem 0;
  color: var(--color-dark-textSecondary);
}
.reveal-text.small { 
  font-size: .9rem; 
}

.mortgage-sheet { 
  display: flex; 
  justify-content: center; 
  gap: 2rem; 
  margin: 1rem 0 1.5rem; 
  font-size: .9rem;
  background: var(--color-dark-bgTertiary);
  padding: 1rem;
  border-radius: var(--radius-md);
  border: 1px solid var(--color-dark-border);
}
.mortgage-sheet table { 
  border-collapse: collapse; 
}
.mortgage-sheet td { 
  padding: 2px 6px;
  color: var(--color-dark-text);
}
.mortgage-sheet h4 { 
  margin-bottom: .25rem;
  color: var(--color-dark-text);
}
